import React, { FC } from "react";

import { Icon, IconName } from "../../Icon/Icon";

import {
	RightBar,
	StyledLocalNavigationItem,
	StyledLocalNavigationItemProps,
} from "./LocalNavigationItem.styles";

export interface LocalNavigationItemProps extends StyledLocalNavigationItemProps {
	dataQA: string
	navigationItemName?: string
	navigationItemIcon?: IconName
}

const LocalNavigationItem: FC<LocalNavigationItemProps> = ({
	dataQA,
	isActive,
	navigationItemName,
	navigationItemIcon,
	isParent,
	isDisabled,
}) => (
	<StyledLocalNavigationItem
		isActive={isActive}
		dataQA={dataQA}
		variant="bodyCopySmallBold"
		component="p"
		isParent={isParent}
		isDisabled={isDisabled}
	>
		{navigationItemIcon && <Icon
			size="17"
			name={navigationItemIcon}
			aria-hidden="true"
		/>}
		{navigationItemName}
		{isActive && (
			<RightBar
				isActive={isActive}
				data-qa={`${dataQA}-right-bar`}
			/>
		)}
	</StyledLocalNavigationItem>
);

LocalNavigationItem.displayName = "LocalNavigationItem";
export default LocalNavigationItem;
